header {
  background-color: rgba(255, 255, 255, 0.2);
}

nav {
  display: flex;
  align-items: center;
  ol {
    list-style: none;
    display: flex;
    justify-content: space-around;
    flex: 1;

    a {
      text-decoration: none;
      color: #000;
      padding: 5px 10px;
    }
  }
}